  background-color: rgba(234, 9, 9, 0.1);
  padding: 5px 16px 6px 42px;
  margin-top: 4px;
  border-radius: 5px;
}
#EOA-APP .ss-eoa-validate-message > svg {
  position: absolute;
  top: 7px;
  left: 16px;
}
#EOA-APP .thank-you-note {
  max-width: 180px;
  font-family: "Inter", sans-serif;
}
#EOA-APP .ss-eoa-app-loader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  background-color: rgba(255, 255, 255, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}
#EOA-APP .ss-eoa-app-loader > p {
  text-align: center;
  color: #000;
  font-family: "Inter", sans-serif;
  font-size: 12px;
  line-height: 2em;
  font-weight: bold;
}
#EOA-APP .ss-eoa-loading-spinner {
  display: inline-block;
  width: 32px;
  height: 32px;
  color: #1268b3;
  -webkit-animation: sseoaspin 1s linear infinite;
  -moz-animation: sseoaspin 1s linear infinite;
  animation: sseoaspin 1s linear infinite;
}
#EOA-APP .footer-note-open .ss-eoa-content {
  filter: blur(4px);
  -webkit-filter: blur(4px);
}
@media screen and (max-width: 480px) {
  #EOA-APP {
    bottom: 10px;
  }
  #EOA-APP .ss-eoa-wrapper {
    bottom: -50px;
    height: calc(100vh - 20px);
    left: 0;
    right: 0;
    max-width: inherit;
  }
  #EOA-APP .ss-eoa-content {
    max-height: calc(100vh - 160px);
  }
  #EOA-APP .ss-eoa-note {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  }
}
@-moz-keyframes sseoaspin {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes sseoaspin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes sseoaspin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
